/**/
#logo {
  height: auto;
  background: #1AC4FF; }

nav#primary {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow: hidden;
  background: #f6f8fb; }
  nav#primary > ul {
    margin: 0; }
    nav#primary > ul > li {
      border-bottom: thin solid #e2e4e7; }
      nav#primary > ul > li > a {
        width: 100%;
        margin: 0;
        padding: 0;
        font-family: 'Roboto', sans-serif;
        padding: 10px 10px 10px 20px;
        box-shadow: none;
        text-shadow: none;
        font-size: 1.6rem;
        line-height: 100%;
        font-weight: 400;
        display: block;
        transition: all 0.4s;
        color: #929497; }
        nav#primary > ul > li > a:hover {
          color: #1AC4FF; }
        nav#primary > ul > li > a::after {
          display: none; }
    nav#primary > ul #dashboard-link {
      margin: 0;
      padding: 0; }

.topbar ul li ul {
  background: #fbfdff; }
  .topbar ul li ul li {
    border-bottom: thin solid #eceef1; }
    .topbar ul li ul li a {
      width: 100%;
      margin: 0;
      padding: 0;
      font-family: 'Roboto', sans-serif;
      padding: 10px 10px 10px 30px !important;
      box-shadow: none;
      color: #A6A8AB;
      transition: all 0.4s;
      text-shadow: none;
      font-size: 1.4rem;
      line-height: 100%;
      font-weight: 300;
      display: block;
      background: none; }
      .topbar ul li ul li a:hover {
        color: #1AC4FF;
        background: none; }

.topbar ul li ul li.first {
  margin: 0;
  padding: 0; }

.topbar ul li.open a.menu {
  color: #1AC4FF; }

.topbar ul li a::before {
  font-family: fontAwesome;
  color: #A6A8AB;
  margin-right: 10px;
  transition: all 0.4s; }
.topbar ul li a:hover::before {
  color: #1AC4FF; }
.topbar ul li:nth-of-type(1) a::before {
  content: '\f0e4'; }
.topbar ul li:nth-of-type(1) > ul a::before {
  content: '\f0e4'; }
.topbar ul li:nth-of-type(2) a::before {
  content: '\f0ea'; }
.topbar ul li:nth-of-type(2) > ul a::before {
  content: '\f0ea'; }
.topbar ul li:nth-of-type(3) a::before {
  content: '\f00a'; }
.topbar ul li:nth-of-type(3) > ul a::before {
  content: '\f00a'; }
.topbar ul li:nth-of-type(4) a::before {
  content: '\f233'; }
.topbar ul li:nth-of-type(4) > ul a::before {
  content: '\f233'; }
.topbar ul li:nth-of-type(5) a::before {
  content: '\f0c0'; }
.topbar ul li:nth-of-type(5) a a::before {
  content: '\f0c0'; }
.topbar ul li:nth-of-type(6) a::before {
  content: '\f085'; }
.topbar ul li:nth-of-type(6) a a::before {
  content: '\f085'; }
.topbar ul li:nth-of-type(7) a::before {
  content: '\f0d0'; }
.topbar ul li:nth-of-type(7) > ul a::before {
  content: '\f0d0'; }
.topbar ul li:nth-of-type(8) a::before {
  content: '\f011'; }
.topbar ul li:nth-of-type(8) a a::before {
  content: '\f011'; }

.menu-btn {
  background: none;
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
  margin: 0 0 0 -10px;
  position: relative; }
  .menu-btn::before {
    font-family: fontAwesome;
    color: #A6A8AB;
    font-size: 3rem;
    display: block;
    position: absolute;
    bottom: 5px;
    left: 0;
    margin: 0;
    padding: 0;
    transition: all 0.4s;
    content: '\f061'; }
  .menu-btn:hover::before {
    color: #1AC4FF; }

.menu-btn.active::before {
  content: '\f0c9'; }

.topbar ul li a.menu {
  background: url(../../img/select-arrow.png);
  background-repeat: no-repeat;
  background-position: 220px 2px; }

.topbar ul li.open a.menu {
  background: url(../../img/select-arrow.png);
  background-repeat: no-repeat;
  background-position: 220px 2px; }

.topbar ul li ul li a::before {
  content: '\f192' !important;
  font-size: 1rem;
  line-height: 100%;
  font-weight: normal; }

.btn.small {
  padding: 0 10px;
  height: 33px !important;
  font-size: 1.4rem;
  line-height: 33px;
  font-weight: 300;
  border-radius: 4px; }

.btn.blue {
  background: #1AC4FF; }

.langs {
  text-align: right;
  margin: 10px; }
  .langs span {
    display: inline-block;
