    vertical-align: top;
    white-space: normal; }
  .langs img {
    height: 30px;
    width: 30px; }

.leads h2 {
  color: #56585b; }
.leads .text {
  font-size: 1.6rem;
  line-height: 150%;
  font-weight: 300;
  color: #56585b; }
.leads .btn-floating {
  margin-bottom: 20px; }
.leads .block {
  display: inline-block;
  position: relative;
  box-shadow: none;
  width: auto;
  overflow: hidden; }
.leads tfoot {
  display: none; }
.leads table tbody tr:nth-child(2n+1) td {
  background: #f6f6f6 !important; }

.myAdds h2 {
  color: #56585b; }
.myAdds .add {
  margin-bottom: 10px;
  height: 150px; }

